.swiper-column-auto .swiper-slide {
  width: calc((100% - var(--mr) * (var(--spv) - 1)) / var(--spv));
  @apply h-auto;
}
.swiper-column-auto .swiper-slide:not(:last-child) {
  margin-right: var(--mr);
}
@screen md {
  .swiper-column-auto.auto-2-column .swiper-slide {
    --spv: 2;
  }
}
@screen md {
  .swiper-column-auto.auto-3-column .swiper-slide {
    --spv: 2;
  }
}
@screen lg {
  .swiper-column-auto.auto-3-column .swiper-slide {
    --spv: 3;
  }
}
@media (max-width: 767.98px) {
  .swiper-column-auto.auto-3-column.show-half-mobile .swiper-slide {
    width: 80%;
  }
}
@screen md {
  .swiper-column-auto.auto-3-column-custom .swiper-slide {
    --spv: 2;
  }
}
@screen lg {
  .swiper-column-auto.auto-3-column-custom .swiper-slide {
    --spv: 3;
    --mr: 40px;
  }
}
@media (max-width: 767.98px) {
  .swiper-column-auto.auto-3-column-custom.show-half-mobile .swiper-slide {
    width: 80%;
  }
}
.swiper-column-auto.auto-4-column .swiper-slide {
  --spv: 1.5;
}
@screen sm {
  .swiper-column-auto.auto-4-column .swiper-slide {
    --spv: 2.5;
  }
}
@screen lg {
  .swiper-column-auto.auto-4-column .swiper-slide {
    --spv: 3.5;
  }
}
@screen xl {
  .swiper-column-auto.auto-4-column .swiper-slide {
    --spv: 4;
  }
}
@screen sm {
  .swiper-column-auto.auto-5-column .swiper-slide {
    --spv: 2;
  }
}
@screen md {
  .swiper-column-auto.auto-5-column .swiper-slide {
    --spv: 3;
  }
}
@screen lg {
  .swiper-column-auto.auto-5-column .swiper-slide {
    --spv: 4;
  }
}
@screen xl {
  .swiper-column-auto.auto-5-column .swiper-slide {
    --spv: 5;
  }
}
.swiper-column-auto.auto-6-column .swiper-slide {
  --spv: 2;
}
@screen md {
  .swiper-column-auto.auto-6-column .swiper-slide {
    --spv: 3;
  }
}
@screen lg {
  .swiper-column-auto.auto-6-column .swiper-slide {
    --spv: 4;
  }
}
@screen xl {
  .swiper-column-auto.auto-6-column .swiper-slide {
    --spv: 6;
  }
}
@media (max-width: 575.98px) {
  .swiper-column-auto.show-half-mobile .swiper-slide {
    width: 80%;
  }
}
.swiper-column-auto.allow-touchMove .swiper-slide {
  @apply cursor-grab;
}