.section-about-5
	.container
		.block-title
			@apply flex flex-col gap-base pb-10 border-b border-utility-gray-200-line
		.block-item
			@apply flex flex-col gap-base py-10 border-b border-utility-gray-200-line
			.heading-6
				@apply flex gap-4 items-center
				&::before
					content: ""
					@apply w-[4px] h-[4px] bg-utility-gray-300
			.item-grid
				@apply grid grid-cols-2 gap-base
				@screen md
					@apply grid-cols-3
				@screen lg
					@apply grid-cols-4
				@screen xl
					@apply grid-cols-6
