.section-about-6
	.bg-about-6
		@apply relative z-0 bg-blend-luminosity bg-utility-gray-800
		&::after
			content: ""
			@apply bg-utility-gray-100/20 absolute w-full h-full top-0 left-0 inset-0
	.block-left
		@apply rem:pr-[87px]
		@screen -xl
			@apply pr-0
		.swiper-detail-product-thumb
			.swiper-slide-thumb
				@apply border border-utility-gray-200-line
			.swiper-slide-thumb-active
				@apply border border-primary-7
		.swiper-scrollbar
			@apply absolute top-0 rem:left-[calc(100%+12px)] -lg:hidden

			.swiper-scrollbar-drag
				@apply bg-primary-7
	.block-right

		@apply gap-base flex flex-col justify-center -lg:flex-col-reverse
		@screen lg
			@apply gap-20
		.block-big
			@apply gap-base flex flex-col justify-center
			@screen lg
				@apply gap-20
			.sup-title
				@apply flex flex-col gap-6
		.button-swiper
			@apply flex gap-[18px] -lg:justify-center
