
.section-about-1
	.container
		.block-content
			@apply flex flex-col gap-base justify-center items-center rem:min-h-[242px] px-[10%] -lg:px-0
			.body-1
				@apply text-utility-gray-800 text-center rem:px-[123px] -lg:px-0
		.block-logo-bg
			filter: grayscale(100%)
			@apply absolute-center w-full h-full pointer-events-none px-[10%] top-[60%]
			&::after
				content: ""
				@apply absolute top-0 left-0 h-full w-full
