header
	@apply sticky  top-0 z-100 bg-white
	.block-header
		@apply flex justify-between
		.hamburger-mobile
			@apply p-2
		.header-logo
			@apply  py-4 w-full rem:max-h-[30px]
		.header-right
			@apply flex rem:gap-[22px]
			.header-menu
				@apply -lg:hidden
				&>ul
					@apply flex rem:gap-6
					&>li
						@apply relative
						&[class*='current']
							&>a
								@apply text-primary-1
						&>a
							@apply hover:text-primary-1 whitespace-nowrap heading-3 py-5 block w-full
						&:hover
							&>ul
								@apply opacity-100 visible
						&>ul
							@apply absolute  top-full left-0 rem:w-[258px] p-6 flex flex-col gap-y-4 bg-white rounded-b-2 opacity-0 invisible transition-300
							&::before
								content: ""
								@apply absolute -top-3 left-0 w-full h-3
							&>li
								@apply body-1 capitalize py-0 font-normal whitespace-nowrap
								a
									&:hover
										@apply text-primary-1
								&[class*='current']
									&>a
										@apply text-primary-1

			.header-icon
				@apply flex gap-5 py-4
				button
					@apply w-8 h-8 bg-utility-gray-100 rounded-full text-primary-7 text-base font-bold duration-300
				ul
					@apply relative inline-block overflow-hidden
					&:hover
						@apply overflow-visible
					li
						@apply relative
						a
							@apply flex justify-center items-center text-0
							img
								@apply w-8 h-8 rounded-full
					&>ul
						@apply top-full right-0 absolute pt-2 opacity-0 pointer-events-none translate-y-2 transition-300 duration-300
					&:hover
						&>ul
							@apply opacity-100 pointer-events-auto translate-y-0
