
.page-banner-main,.swiper
	.swiper-pagination
		@apply absolute bottom-[10%] w-auto h-4 right-[calc(80/1920*100%)] -lg:right-1/2 -lg:translate-x-1/2 left-[unset] -lg:bottom-[2%] flex items-center
	.swiper-pagination-bullet
		@apply bg-white rounded-full w-2 h-2 border-2 border-white  relative opacity-100
	.swiper-pagination-bullet-active
		@apply w-4 h-4 bg-transparent
		&::before
			content: ""
			@apply w-2 h-2 bg-white rounded-full absolute-center
.swiper-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal
	@apply flex gap-2
