.section-contact
	.contact
		.form-contact
			.wrap-inp
				.block-inp
					.style-inp
						@apply w-full rem:h-[49px] px-4 py-3 border-none outline-none rounded-lg bg-utility-gray-50
					.style-textarea
						@apply w-full rem:h-[183px] px-4 py-3 border-none outline-none rounded-lg bg-utility-gray-50
		.block-contact-info
			.contact-info
				.contact-info-list
					.contact-info-item
						&:first-child
							.block-content
								.content
									@apply break-normal
									a
										br
											@apply -lg:hidden
