@layer components
	form.submitting
		button
			@apply pointer-events-none relative
			&::before
				content: ''
				animation-duration: 0.5s
				@apply w-5 h-5 border-3 border-primary-1/40 border-t-primary-1 rounded-full inline-block
				@apply absolute animate-spin left-full ml-3
	#popup-applyNow
		@apply opacity-0 invisible transition-500
		.wrap-popup-applyNow
			.form-register-applyNow
				.last-inp
					.block-choseFile
						@apply flex gap-4 items-center mt-5
						label
							@apply flex -sm:flex-col items-start sm:items-center gap-2 mt-2
						.choseFile-btn
							@apply relative rounded-lg border border-primary-1 py-2 px-3.5 select-none
							.style-btn-file
								@apply absolute w-full h-full inset-0 opacity-0 cursor-pointer
							.content
								@apply body-2 uppercase text-primary-1 whitespace-nowrap
						.allow-file-content
							@apply body-1 text-utility-gray-500
				.block-inp
					.style-inp
						@apply w-full rem:h-[45px] px-5 py-2.5 border-none outline-none rounded-lg bg-utility-gray-50
	body
		&.isOpentPopupApplyNow
			#modal
				@apply opacity-100 visible
			#popup-applyNow
				@apply opacity-100 visible
		&.isOpenMenu
			#modal
				@screen -lg
					@apply opacity-100 visible
			@screen -lg
				.menuMobile
					@apply opacity-100 visible translate-x-0
