.btn
	@apply cursor-pointer select-none transition-all duration-300 border border-primary-1 text-primary-1
	&:hover
		@apply text-white bg-primary-1
.btn-next
	@apply w-10 h-10 flex-center title-24 rounded-2
.btn-prev
	@apply w-10 h-10 flex-center title-24 rounded-2
.btn-primary
	@apply w-fit px-4 py-2.5 rounded-full
	&:hover,&.active
		@apply text-white bg-primary-1
