.section-project-list
	.block-content,.block-project
		@apply flex flex-col gap-base
		.content-project
			@apply   text-utility-gray-800 rem:max-w-[1166px]
		.block-project
			.list-project
				@apply overflow-x-auto
				&::-webkit-scrollbar
					@apply  w-[2px] h-[4px]
				&::-webkit-scrollbar-track
					@apply bg-gray-300 rounded-4
				&::-webkit-scrollbar-thumb
					@apply bg-primary-1  rounded-4
				ul
					@apply flex mb-1
					li
						@apply heading-4 flex items-center gap-4 whitespace-nowrap
						&::after
							content: ""
							@apply w-1 h-1 bg-utility-gray-300 mr-4
						&:last-child
							&::after
								content: ""
								@apply w-0 h-0  mr-0
						&.active
							@apply text-primary-1
						&:hover
							@apply text-primary-1
