
.section-new-detail
	.new-line,.date
		@apply flex gap-5 items-center
		.date
			p
				@apply body-1
				&:first-child
					@apply text-primary-7
		.new-line
			@apply flex-1 h-[1px] bg-primary-7
	.block-new-icon
		@apply fixed left-[calc(180/1920*100rem)] top-[25%] flex flex-col gap-3
		.icon-item
			@apply w-10 h-10 bg-primary-1 text-white rounded-full flex-center
			&:hover
				@apply  text-primary-7
