.bg-img-footer
	&::before
		content: ""
		@apply absolute top-0 left-0 w-full h-full bg-black bg-opacity-50
	.main-content
		@apply w-full  section-mx -sm:mx-2 bg-[#cecece] bg-opacity-30 backdrop-blur-[10px] rounded-5 -lg:mt-base -lg:mb-base
		.block-footer
			@apply flex-col-reverse  text-white section-py
			@screen lg
				@apply py-14 mb-5 flex flex-row justify-between
			.block-footer-right,.block-footer-left
				@screen lg
					@apply ml-8
			.block-footer-right
				@apply flex flex-col gap-base
				.footer-top
					@apply flex justify-between -md:flex-col
					.block-content-left
						ul
							@apply flex flex-col gap-3
							li
								&[class*='current']
									a
										&::after
											@apply opacity-100 duration-300 w-full
								a
									@apply pb-1 inline-block
									&::after
										content: ""
										@apply  border-b border-white opacity-0  w-0 transition-all block
									&:hover::after
										content: ""
										@apply opacity-60 duration-300 w-full
						.block-menu-footer
							@apply flex flex-col gap-5
							ul
								@apply flex flex-col gap-3 heading-5
					.block-content-right
						@apply flex flex-col gap-5
						ul
							@apply flex flex-col gap-5
							li
								@apply flex gap-4
								.icon-info
									@apply w-12 h-12 bg-black/10 rounded-full flex-center title-20
								.block-content-link
									@apply flex flex-col flex-1
									a
										@apply hover:text-primary-1
				.footer-bottom
					@apply flex justify-between pt-5 border-t border-white/25
					.body-2
						@apply p-2.5
					.block-icon
						@apply flex gap-2
						.block-icon-item
							@apply w-10 h-10 flex-center border border-white rounded-full hover:bg-primary-7
			.block-footer-left
				.block-content
					@apply flex flex-col gap-base
					.logo-footer
						@apply mb-1
					.block-contact
						@apply flex flex-col gap-3
						.body-1
							@apply  text-opacity-90
					.block-from
						@apply grid grid-cols-2 gap-5 mb-3
						.block-input
							@apply border border-white border-opacity-25 w-full rounded-1
							input
								@apply bg-transparent p-2  outline-none  w-full
							input::placeholder
								@apply  body-3 text-white focus:text-opacity-75 pointer-events-none
							&:last-child
								@apply col-span-full flex justify-between items-center
								button
									@apply flex justify-center items-center w-8 h-8 bg-white rounded-1 text-primary-7 mr-1
									&:hover
										@apply text-white bg-primary-7
								input
									@apply w-full bg-transparent
