.section-home-1
	.main-content-left
		@apply relative
		.img
			&::after
				content: ""
				@apply absolute top-0 left-0 w-full h-full bg-black/50
		.content-img-top,
		.content-img-botom
			@apply absolute-center-x z-1 text-white w-full
		.content-img-top
			@apply top-15 px-10
		.content-img-botom
			@apply bottom-5
			a
				@apply hover:text-primary-1
	.block-home-left
		.button-swiper
			@apply absolute-center-y -right-[6%] flex flex-col gap-base
			@screen -lg
				@apply static flex-row-reverse mt-10 justify-center

		.content-img-botom
			@apply absolute-center-x z-1 bottom-5 text-white
