.section-home-2
	.home-2-gird
		@apply grid grid-cols-2 gap-base
		@screen -lg
			@apply grid-cols-1 bg-utility-gray-50 section-px section-py rounded-4
		.wrap
			@apply bg-utility-gray-50 rounded-4 px-10 rem:pt-[101px]
			@screen -lg
				@apply p-0 order-2
			ul
				@apply flex flex-col gap-3
				li
					@apply bg-white/50 w-full px-5 pt-3 rounded-2
					a
						.heading-2
							@apply pb-2.5
					.content-hiden
						transform: translateY(8px)
						@apply mt-2.5 h-0 opacity-0 overflow-hidden transition-all duration-500 ease-linear flex flex-col gap-2.5 pointer-events-none
					&.active
						@apply bg-white
						a
							.heading-2
								@apply text-primary-7 border-b border-utility-gray-200-line
						.content-hiden
							@apply h-auto opacity-100 font-normal text-utility-gray-800 pointer-events-auto
		.tabslet-content
			animation: fadeIn 1.2s ease-in-out
@keyframes fadeIn
	from
		opacity: 0.5
	to
		opacity: 1
