.global-breadcrumb
	@apply   bg-utility-gray-50
	p
		@apply h-[calc(48/1920*100rem)] flex items-center overflow-auto
		a
			&:first-child
				@apply body-2 text-utility-gray-500
		a,span
			@apply body-2 whitespace-nowrap text-utility-gray-500
		span.separator
			@apply text-0 inline-flex
			&::before
				content: "\f054"
				@apply font-awesome font-normal text-sm mx-3
