.header-search-form
	visibility: hidden
	@apply fixed rem:top-[70px] left-0 w-full h-full bg-black bg-opacity-30 backdrop-blur-md z-[1000] flex justify-center items-center transition-all duration-300 opacity-0  pointer-events-none
	&.active
		@apply opacity-100 visible pointer-events-auto
	.close
		@apply m-3
	.wrap-form-search-product
		max-width: r(800px)
		margin: 0 auto
		@apply w-full
		.productsearchbox
			@apply w-full flex relative flex-wrap
			[class*="Module"]
				width: 100% !important
			input
				@apply flex-1 h-12.5 bg-white text-base px-7.5 outline-0 rounded-l-2 #{!important}
			button
				@apply w-12.5 flex items-center justify-center  outline-0  rounded-r-2 border-l border-[#E0E0E0] bg-primary-1 text-white
				&::after
					content: '\f002'
					@apply font-awesome font-light text-2xl
