.section-project-Detail-3
	@apply px-0   -md:section-px
	.swiper-slide
		@apply relative
		&::after
			content: ""
			@apply absolute top-0 left-0 w-full h-full bg-white/50 transition-all duration-500 ease-in-out
	.swiper-slide-active
		@apply relative
		&::after
			content: ""
			@apply absolute top-0 left-0 w-full h-0 bg-transparent
	.btn-detail
		@apply md:absolute-center-y z-10
	.btn-detail.btn-next-detail
		@apply right-[calc(180/1920*100rem)]
	.btn-detail.btn-prev-detail
		@apply left-[calc(180/1920*100rem)]
