.section-new-list
	.block-content-top
		@apply flex justify-between items-center flex-wrap -lg:gap-base
		.list-new
			@apply overflow-x-auto
			&::-webkit-scrollbar
				@apply  w-[2px] h-[4px]
			&::-webkit-scrollbar-track
				@apply bg-gray-300 rounded-4
			&::-webkit-scrollbar-thumb
				@apply bg-primary-1  rounded-4
			ul
				@apply flex mb-1
				li
					@apply heading-4 flex items-center gap-4 whitespace-nowrap -md:body-1
					&::after
						content: ""
						@apply w-1 h-1 bg-utility-gray-300 mr-4
					&:last-child
						&::after
							content: ""
							@apply w-0 h-0  mr-0
					&.active
						@apply text-primary-1
					&:hover
						@apply text-primary-1
	.block-new-banner
		.bg-new
			.block-content
				@apply   mr-0 xl:mr-10 flex flex-col gap-6
				.date
					@apply flex  body-2 gap-2 w-full pb-1 border-b border-utility-gray-200-line
					span
						@apply text-primary-7
				.title-new
					@apply text-gray-950 font-bold leading-1.3 text-3xl
					&:hover
						@apply text-primary-7
				// .body-1
	.block-new-card
		.card-new
			.new-content
				.date
					@apply flex gap-2
					span
						@apply text-primary-7
